import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';

import {
  setCharacter
} from '../../features/characters/characterSlice';


import {
  useParams
} from "react-router-dom";

function CharacterPage(props) {
  const dispatch = useDispatch();

  let { id } = useParams();
  // let character;

  let [character, setStateCharacter] = useState();
  let [loader, setLoaderState] = useState(true);
  useEffect(() => {
    fetch('https://t7frames-server.herokuapp.com/frame-data/')
    .then((res) => res.json())
    .then(data => {
      if (data[id]) {
        // dispatch(setCharacter(data[id]));
        setStateCharacter(data[id]);
        setLoaderState(false);
      }
    });
  }, []);
 

  let char = useSelector((state) => state);
  // console.log(character?.moves[0].Command);
  let movesComponents = [];
  movesComponents = character?.moves.map((move, index) => {
    return <MoveContainer key={index} command={move.Command} />
  });
  return (
    <div className="character-page">
        <h2 className="title">{character?.character}</h2>
        {movesComponents}
        {loader ? <div className="loader"></div> : ''}
    </div>
  );
}

function MoveContainer({command}) {
  return (
    <div className="move-container">
      {command}
    </div>
  );
}

export default CharacterPage;